@import url(./vendor/animate.css);
@import url(./vendor/swiper.css);
:root {
  --color-black: #000;
  --color-white: #fff;
  --color-white-lilac: #F4F5FA;
  --color-emerald: #46C578;
  --color-emerald-hover: #248A4C;
  --color-emerald-pressed: #095527;
  --color-mine-shaft: #303030;
  --color-raven: #73798C;
  --color-wistful: #A7AED2;
  --color-gallery: #F0F0F0;
  --color-coral-red: #FF3D3D;
  --color-mojo: #C54646;
  --color-cod-gray: #121212;
  --color-mercury: #E2E2E2;
  --color-golden-tainoi: #FFD057;
  --color-silver-chalice: #ABABAB;
  --color-french-gray: #C0C2CB;
  --color-alto: #d6d5d5;
  --more-width: 160px;
  --more-margin: 0px;
  --menu-margin: 15px;
  --screen-sm: 576px;
  --screen-md: 768px;
  --screen-lg: 992px;
  --screen-xl: 1200px;
  --screen-sm-max: 767px;
  --screen-md-max: 991px;
  --screen-lg-max: 1199px;
}

