@font-face {
  font-family: "Museo Sans Cyrl";
  src: url("../../fonts/MuseoSansCyrl-300.woff2") format("woff2"), url("../../MuseoSansCyrl-300.woff") format("woff");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Museo Sans Cyrl";
  src: url("../../fonts/MuseoSansCyrl-500.woff2") format("woff2"), url("../../MuseoSansCyrl-500.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Museo Sans Cyrl";
  src: url("../../fonts/MuseoSansCyrl-700.woff2") format("woff2"), url("../../MuseoSansCyrl-700.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Museo Sans Cyrl";
  src: url("../../fonts/MuseoSansCyrl-900.woff2") format("woff2"), url("../../MuseoSansCyrl-900.woff") format("woff");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}

.checkbox-item {
  max-width: fit-content;
  display: flex;
  align-items: center;
  font-weight: 400;
  cursor: pointer;
}

.checkbox-item__input {
  display: none;
}

.checkbox-item__input:checked + .checkbox::after {
  opacity: 1;
}

.checkbox-item__input:checked + .checkbox {
  background-color: var(--color-emerald);
}

.checkbox-item__title {
  font-size: 18px;
  line-height: 22px;
  margin-left: 10px;
}

.checkbox-item__title span {
  color: var(--color-emerald);
  transition: .3s;
}

.checkbox-item__title span:hover {
  color: var(--color-emerald-hover);
}

.checkbox-item .checkbox {
  width: 20px;
  height: 20px;
  background-color: var(--color-white-lilac);
  border-radius: 5px;
  position: relative;
  padding-right: 20px;
}

.checkbox-item .checkbox::after {
  content: "";
  width: 16px;
  height: 16px;
  background-image: url("../img/sprite.svg#check-icon");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
}

.select {
  margin-top: 10px;
  position: relative;
}

.select .swiper-scrollbar {
  margin-right: 5px;
  margin-top: 20px;
  margin-bottom: 20px;
  background: none;
  width: 4px;
}

.select .swiper-scrollbar-drag {
  background-color: var(--color-wistful);
}

.select__head {
  width: 100%;
  height: 50px;
  border: 1px solid var(--color-wistful);
  border-radius: 10px;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
  user-select: none;
}

.select__current {
  font-family: "Museo Sans Cyrl";
  font-weight: 300;
  font-size: 16px;
  line-height: 100%;
  color: var(--color-wistful);
}

.select__icon {
  width: 16px;
  height: 16px;
}

.select__body {
  width: 100%;
  background-color: var(--color-white);
  box-shadow: 0px 4px 10px var(--color-white-lilac);
  border-radius: 15px;
  position: absolute;
  top: 60px;
  z-index: 10;
  max-height: 274px;
  padding: 15px 15px 0 15px;
  visibility: hidden;
}

.select__title {
  font-family: "Museo Sans Cyrl";
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  margin-bottom: 15px;
}

.select__item {
  font-size: 18px;
  line-height: 22px;
  margin-bottom: 15px;
  cursor: pointer;
}

.select--active .select__body {
  visibility: visible;
}

.input {
  margin-bottom: 20px;
}

.input__title {
  font-family: "Museo Sans Cyrl";
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
}

.input__field {
  width: 100%;
  height: 50px;
  border: 1px solid var(--color-wistful);
  border-radius: 10px;
  outline: none;
  padding: 0 15px;
  font-family: "Museo Sans Cyrl";
  font-weight: 300;
  font-size: 16px;
  line-height: 100%;
  color: var(--color-mine-shaft);
  margin-top: 10px;
}

.input__field::placeholder {
  font-family: "Museo Sans Cyrl";
  font-weight: 300;
  font-size: 16px;
  line-height: 100%;
  color: var(--color-wistful);
}

.input__text-area {
  width: 100%;
  height: 235px;
  resize: none;
  border: 1px solid var(--color-wistful);
  border-radius: 10px;
  outline: none;
  padding: 17px 15px 17px 15px;
  font-family: "Museo Sans Cyrl";
  font-weight: 300;
  font-size: 16px;
  line-height: 100%;
  color: var(--color-mine-shaft);
  margin-top: 10px;
}

.input__text-area::placeholder {
  font-family: "Museo Sans Cyrl";
  font-weight: 300;
  font-size: 16px;
  line-height: 100%;
  color: var(--color-wistful);
}

.input__selects {
  width: 100%;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(3, 1fr);
}

.input__field--hidden {
  display: none;
}

.input__error-log {
  margin-top: 10px;
  font-family: "Museo Sans Cyrl";
  font-weight: 300;
  font-size: 16px;
  line-height: 100%;
  color: var(--color-coral-red);
  display: none;
}

.input__checkbox {
  margin-top: 10px;
}

.input__checkbox .checkbox-item__title {
  font-family: "Museo Sans Cyrl";
  font-weight: 300;
  font-size: 14px;
  line-height: 100%;
}

.input--hidden {
  display: none;
}

.input:last-child {
  margin-bottom: 0;
}

.input--disabled .input__title {
  color: var(--color-wistful);
}

.input--disabled .input__field:disabled {
  background-color: var(--color-white);
  color: var(--color-wistful);
}

.input--error .input__field {
  border: 1px solid var(--color-coral-red);
}

.input--error .input__error-log {
  display: flex;
}

.mobile-menu {
  width: 100%;
  position: fixed;
  left: 50%;
  transform: translate(-50%, 0);
  bottom: 0;
  z-index: 20;
  display: none;
  grid-gap: 0;
  grid-template-columns: repeat(4, 1fr);
  padding: 10px 15px;
  background-color: var(--color-white);
  box-shadow: 0px -27px 355px rgba(0, 0, 0, 0.0174206), 0px -12.8911px 132.878px rgba(0, 0, 0, 0.025), 0px -5.0507px 49.0332px rgba(0, 0, 0, 0.0325794), 0px -1.56836px 16.6433px rgba(0, 0, 0, 0.05);
}

@media (max-width: 991px) {
  .mobile-menu {
    display: grid;
  }
}

.mobile-menu__item {
  padding: 0 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mobile-menu__link {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mobile-menu__link--active .mobile-menu__link-icon svg {
  fill: var(--color-emerald);
}

.mobile-menu__link--active .mobile-menu__link-label {
  color: var(--color-emerald);
}

.mobile-menu__link--notify .mobile-menu__link-notify {
  display: flex;
}

.mobile-menu__link-notify {
  display: none;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -6px;
  right: -6px;
  width: 12px;
  height: 12px;
  background-color: var(--color-emerald-pressed);
  border-radius: 50%;
  font-size: 7px;
  font-weight: 400;
  text-align: center;
  color: var(--color-white);
}

.mobile-menu__link-icon {
  position: relative;
}

.mobile-menu__link-icon svg {
  fill: var(--color-wistful);
}

.mobile-menu__link-label {
  margin-top: 5px;
  font-family: "Museo Sans Cyrl";
  font-weight: 300;
  font-size: 10px;
  line-height: 100%;
  text-align: center;
  color: var(--color-wistful);
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  background-color: var(--color-white);
  width: 100%;
  transform: none;
  font-family: "Museo Sans Cyrl";
  font-weight: 400;
  font-style: normal;
  color: var(--color-mine-shaft);
}

.main {
  width: 100%;
  transform: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

@media (max-width: 767px) {
  .footer-hidden {
    display: none;
  }
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: var(--color-mine-shaft);
}

ul {
  list-style: none;
  padding: 0;
}

button {
  cursor: pointer;
  outline: none;
}

button[disabled] {
  background: #bdbdbd;
  cursor: default;
}

button[disabled]:hover {
  background: #bdbdbd;
}

.btn {
  background-color: var(--color-emerald);
  border-radius: 10px;
  color: var(--color-white);
  transition: 0.5s;
  text-align: center;
  text-decoration: none;
  border: 0;
  max-width: 380px;
}

.btn:hover {
  background-color: var(--color-emerald-hover);
}

.btn:active {
  background-color: var(--color-emerald-pressed);
}

.search-bg {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(48, 48, 48, 0.4);
  width: 100%;
  height: calc(100vh - 97px);
  z-index: 3;
  display: none;
}

.search-bg--active {
  display: flex;
}

.h1 {
  font-family: "Museo Sans Cyrl";
  font-weight: 900;
  font-size: 32px;
  line-height: 38px;
  color: var(--color-mine-shaft);
  margin-bottom: 20px;
}

.title {
  font-family: "Museo Sans Cyrl";
  font-weight: 700;
  font-size: 42px;
  line-height: 50px;
  margin-bottom: 30px;
  color: var(--color-black);
}

@media (max-width: 767px) {
  .title {
    font-size: 24px;
    line-height: 100%;
  }
}

.breadcrumbs {
  margin-top: 20px;
  font-family: "Museo Sans 500 Medium";
  font-size: 14px;
  line-height: 17px;
  color: var(--color-wistful);
  text-decoration: none;
}

.breadcrumbs a {
  color: var(--color-wistful);
}

.form-additional {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 22px;
}

.form-additional__link {
  font-family: "Museo Sans Cyrl";
  font-weight: 300;
  font-size: 14px;
  line-height: 100%;
}

.checkbox-item--form {
  margin: 0;
}

.checkbox-item--form .checkbox-item__title {
  font-family: "Museo Sans Cyrl";
  font-weight: 300;
  font-size: 14px;
  line-height: 100%;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.container {
  display: flex;
  width: 100%;
  max-width: 1180px;
  height: 100%;
  margin: 0 auto;
  flex-direction: inherit;
  justify-content: inherit;
  align-items: inherit;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.header {
  width: 100%;
}

.header__container {
  display: flex;
  flex-direction: column;
}

.header .logo svg {
  width: 99px;
  height: 42px;
}

@media (max-width: 767px) {
  .header .logo {
    display: flex;
  }
  .header .logo svg {
    width: 85px;
    height: 35px;
  }
}

@media (max-width: 767px) {
  .header .logo--centered {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
}

.header__back {
  line-height: 100%;
  color: var(--color-mine-shaft);
  position: relative;
  padding-right: 46px;
  font-size: 14px;
}

@media (max-width: 767px) {
  .header__back {
    padding-right: 37px;
    font-size: 12px;
    margin-bottom: 6px;
  }
}

.header__back::before {
  content: "";
  width: 36px;
  height: 36px;
  background-size: 32px;
  background-color: var(--color-emerald);
  background-image: url("../img/sprite.svg#close-icon");
  background-repeat: no-repeat;
  background-position: center;
  fill: var(--color-white);
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 50%;
}

@media (max-width: 767px) {
  .header__back::before {
    width: 24px;
    height: 24px;
    background-size: 20px;
  }
}

.header__top {
  width: 100%;
  height: 97px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid var(--color-gallery);
  position: relative;
  z-index: 4;
}

@media (max-width: 767px) {
  .header__top {
    height: 64px;
    padding-bottom: 16px;
  }
}

.header__top--sm {
  border: none;
  height: 82px;
}

@media (max-width: 767px) {
  .header__top--sm {
    height: 65px;
    align-items: flex-end;
    border-bottom: 1px solid var(--color-gallery);
  }
}

.header__top-left {
  display: flex;
  align-items: center;
}

.header__phone {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 51px;
  font-family: "Museo Sans Cyrl";
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: var(--color-mine-shaft);
}

@media (max-width: 767px) {
  .header__phone {
    display: none;
  }
}

.header__phone span {
  margin-bottom: 4px;
  font-family: "Museo Sans Cyrl";
  font-weight: 400;
